<template>
<div class="layout">
  <div class="header">活动列表</div>
  <el-button @click="createActivity">创建活动</el-button>
  <div class="table">
    <el-table :data="list" style="width: 100%">
      <el-table-column prop="name" label="活动名称" width="120"></el-table-column>
      <el-table-column prop="sign" label="活动标识" width="120"></el-table-column>
      <el-table-column prop="api_get" label="同步积分接口" width="250"></el-table-column>
      <el-table-column prop="api_post" label="修改积分接口" width="250"></el-table-column>
      <el-table-column prop="url" label="活动系统主页" width="250"></el-table-column>
      <el-table-column label="操作" width="100">
        <template scope="scope">
          <el-button @click="putActivity(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</div>
</template>

<script>
export default {
  props: [
    'list'
  ],
  components: {
  },
  methods: {
    createActivity () {
      this.$store.commit('createActivity', {})
    },
    putActivity (row, column) {
      this.$store.commit('createActivity', row)
    }
  }
}
</script>

<style>

</style>



